import React, {useState} from "react";
import Grid from "@material-ui/core/Grid";
import Base from "../misc/Base";
import BlogDetail from "./BlogDetail";
import MainFeaturedPost from "./MainFeaturedPost";

function BlogDetailPage(props) {
  const blogSlug = props.match.params["blogDetail"];
  const [_blogDetail, _setBlogDetail] = useState(null);

  return (
    <Base>
      <MainFeaturedPost post={_blogDetail ? (_blogDetail.additional[0] ? _blogDetail.additional[0] : {}) : {}}/>

      <Grid container spacing={5}>
        <BlogDetail blogSlug={blogSlug} dataSetter={_setBlogDetail}/>
      </Grid>
    </Base>
  );
}

export default BlogDetailPage;
