import React, {useEffect, useState} from "react";
import Base from "../misc/Base";
import Grid from "@material-ui/core/Grid";
import FeaturedPost from "./FeaturedPost";
import Axios from "axios";
import {Button} from "@material-ui/core";
import {baseUrl} from "../misc/constants";


function BlogListViewPage(props) {
  // current page tracker !!
  const [currentPage, setCurrentPage] = useState(1);
  // setting posts !!
  const [posts, setPosts] = useState([]);

  function getAndSetPosts(_currentPage) {
    // getting posts !!
    if (_currentPage) {
      Axios({
        method: "GET",
        url: `${baseUrl}/api/portfolio/get-posts/`,
        params: {
          ...props.match.params,
          p: _currentPage
        },
      }).then(res => {
        console.log(res.data)
        if (res.data["has_pagination_data"]) {
          setCurrentPage(res.data["pagination"]["next_page_number"]);

          if (_currentPage === 1) {
            setPosts(res.data.posts)
          } else {
            setPosts([...posts, ...res.data.posts]);
          }
        }
      })
    }
  }

  // console.log(props.match.params.q, props.match.params.category)
  useEffect(() => {
    getAndSetPosts(1);
    // eslint-disable-next-line
  }, [props.match.params.q, props.match.params.category])

  return (
    <Base>
      <Grid container spacing={4}>
        {posts.map((post) => (
          <FeaturedPost key={post.slug} post={post}/>
        ))}
      </Grid>
      {currentPage ? (<Button onClick={() => {
        getAndSetPosts(currentPage);
      }}>Load More</Button>) : (
        <div style={{
          paddingTop: "30px"
        }}>
          <Button disabled>
            Seems Like you have come to an end, You can always select different category or search for your interest.
          </Button>
        </div>
      )}
    </Base>
  )
}

export default BlogListViewPage;
