// Main imports !!
import {BrowserRouter, Route, Switch} from "react-router-dom";

// Custom Imports !!
import PortfolioHome from "../portfolio/home";
import Blog from "../blog/Blog";
import BlogDetailPage from "../blog/BlogDetailPage";
import BlogListViewPage from "../blog/BlogListViewPage";
import MainProfile from "../user/profile/MainProfile";

function RouteManager() {
  return (
    <BrowserRouter>
      <Switch square>
        <Route path="/" exact component={PortfolioHome}/>
        <Route path="/blog" exact component={Blog}/>
        <Route path="/blog/search/:q" exact component={BlogListViewPage}/>
        <Route path="/blog/category/:category" exact component={BlogListViewPage}/>
        <Route path="/blog/search/:q/:category" exact component={BlogListViewPage}/>
        <Route path="/blog/:blogDetail" exact component={BlogDetailPage}/>
        <Route path="/profile/:username" exact component={MainProfile}/>
      </Switch>
    </BrowserRouter>
  );
}

export default RouteManager;
