import * as React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import MainFeaturedPost from './MainFeaturedPost';
import FeaturedPost from './FeaturedPost';
import {useContext} from "react";
import Base from "../misc/Base";
import FeaturedBlogContext from "../context/blog/FeaturedBlogContext";
import BlogDetail from "./BlogDetail";

const useStyles = makeStyles((theme) => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
}));

export default function Blog() {
  const classes = useStyles();
  const featuredPosts = useContext(FeaturedBlogContext).featuredPosts;

  return (
    <Base>
      <MainFeaturedPost post={featuredPosts.mainFeaturedPost} postUrl={`/blog/${featuredPosts.mainFeaturedPost.slug}`}/>
      <Grid container spacing={4}>
        {featuredPosts.featuredPosts.map((post) => (
          <FeaturedPost key={post.title} post={post}/>
        ))}
      </Grid>
      <Grid container spacing={5} className={classes.mainGrid}>
        <BlogDetail blogSlug={featuredPosts.mainFeaturedPost.slug}/>
      </Grid>
    </Base>
  );
}
