import * as React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Container from "@material-ui/core/Container";
import Header from "./Header";
import {useContext} from "react";
import BlogCategoryContext from "../context/blog/BlogCategoryContext";

export function Base({categories, children}) {
  const _categories = useContext(BlogCategoryContext).categories;

  return (
    <React.Fragment>
      <CssBaseline/>
      <Container maxWidth="lg">
        <Header title="Blog" sections={categories ? categories : _categories}/>
        <main>
          {children}
        </main>
      </Container>
    </React.Fragment>
  )
}

export default Base;
