import RouteManager from "./misc/routes";
import {FeaturedBlogProvider} from "./context/blog/FeaturedBlogContext";
import {useEffect, useState} from "react";
import Axios from "axios";
import {BlogCategoryProvider} from "./context/blog/BlogCategoryContext";
import {baseUrl} from "./misc/constants";

function App() {
  // For handling featured posts !!
  const [featuredPosts, setFeaturedPosts] = useState({
    mainFeaturedPost: {},
    featuredPosts: []
  });
  // For handling blog categories !!
  const [categories, setCategories] = useState([]);

  // For handling featured posts !!
  useEffect(() => {
    Axios({
      url: `${baseUrl}/api/portfolio/get-featured-posts/`,
      method: "GET"
    }).then(res => {
      let mainBlog = {};
      const secondaryBlogs = [];

      for (const i in res.data["featured_posts"]) {
        if (res.data["featured_posts"].hasOwnProperty(i)) {
          const blog = res.data["featured_posts"][i];

          if (blog.hasOwnProperty("isMainFeaturedPost")) {
            if (blog["isMainFeaturedPost"]) {
              mainBlog = blog
            } else {
              secondaryBlogs.push(blog)
            }
          }
        }
      }

      setFeaturedPosts({
        mainFeaturedPost: mainBlog,
        featuredPosts: secondaryBlogs,
      });
    })
  }, [])

  // For handling categories !!
  useEffect(() => {
    Axios({
      url: `${baseUrl}/api/portfolio/get-category/`,
      method: "GET"
    }).then(res => {
      const _categories = [];

      for (const i in res.data["categories"]) {
        if (res.data["categories"].hasOwnProperty(i)) {
          const _category = res.data["categories"][i];

          _categories.push({
            title: _category.name,
            url: `/blog/category/${_category["slug"]}`,
          });
        }
      }

      setCategories(_categories);
    })
  }, [])

  if (featuredPosts.featuredPosts.length === 0 || categories.length === 0) {
    return (<></>)
  }

  return (
    <FeaturedBlogProvider value={{
      featuredPosts: featuredPosts,
      setFeaturedPosts: setFeaturedPosts
    }}>
      <BlogCategoryProvider value={{
        categories: categories,
        setCategories: setCategories
      }}>
        <div>
          <RouteManager/>
        </div>
      </BlogCategoryProvider>
    </FeaturedBlogProvider>
  );
}

export default App;
