import React, {useEffect, useState} from "react";
import Main from "./Main";
import Sidebar from "./Sidebar";
import Axios from "axios";
import {GetLogoFor} from "../misc/mapper";
import {baseUrl} from "../misc/constants";


function BlogDetail({blogSlug, dataSetter}) {
  // For handling Posts !!
  const [postDetail, setPostDetail] = useState({
    author: "",
    data: [],
    socialUrl: []
  });

  // For handling post detail data !!
  useEffect(() => {
    Axios({
      method: "GET",
      url: `${baseUrl}/api/portfolio/get-home-page-posts/`,
      params: blogSlug ? {slug: blogSlug} : {}
    }).then(res => {
      const _post = [];
      let _user = "";
      let _bio = "";
      const _social = [];
      const _additional = {};

      for (const post in res.data["home_page_posts"]) {
        if (res.data["home_page_posts"].hasOwnProperty(post)) {
          const _data = res.data["home_page_posts"][post];

          for (const account in _data["social"]) {
            if (_data["social"].hasOwnProperty(account)) {
              const _profileUrl = _data["social"][account];
              if (_profileUrl) {
                _social.push(
                  {name: account, icon: GetLogoFor(account), url: _profileUrl}
                )
              }
            }
          }
          _post.push(_data["markdown"]);
          _user = _data["username"];
          _bio = _data["bio"];
          _additional["postTitle"] = _data["title"]
          _additional["creationDate"] = _data["date"]
        }
      }
      // TODO: Work on archives !!
      setPostDetail({
        author: _user,
        bio: _bio,
        socialUrl: _social,
        data: _post,
        ..._additional
      });

      // console.log(res.data)
      if (dataSetter) {
        dataSetter({
          additional: res.data["home_page_posts"],
          author: _user,
          bio: _bio,
          socialUrl: _social,
          data: _post,
        })
      }
    })
    // eslint-disable-next-line
  }, [])

  return (
    <>
      <Main
        title={`From ${postDetail.author}`}
        posts={postDetail.data}
        postTitle={postDetail.postTitle}
        creationDate={postDetail.creationDate}
        username={postDetail.author}
      />
      <Sidebar
        title={`About ${postDetail.author}`}
        description={postDetail.bio}
        social={postDetail.socialUrl}
      />
    </>
  )
}

export default BlogDetail;
